'use strict'

const Buffer = require('../../libs/buffer').Buffer;

const ERROR_CODE = {
	FPNN_EC_PROTO_UNKNOWN_ERROR: 10001,		// 
	FPNN_EC_PROTO_NOT_SUPPORTED: 10002,		// 
	FPNN_EC_PROTO_INVALID_PACKAGE: 10003,	// 
	FPNN_EC_PROTO_JSON_CONVERT: 10004,		// JSON
	FPNN_EC_PROTO_STRING_KEY: 10005,		// 
	FPNN_EC_PROTO_MAP_VALUE: 10006,			// 
	FPNN_EC_PROTO_METHOD_TYPE: 10007,		// 
	FPNN_EC_PROTO_PROTO_TYPE: 10008,		// 
	FPNN_EC_PROTO_KEY_NOT_FOUND: 10009,		// 
	FPNN_EC_PROTO_TYPE_CONVERT: 10010,		// 
			
	FPNN_EC_CORE_UNKNOWN_ERROR: 20001,		// 
	FPNN_EC_CORE_CONNECTION_CLOSED: 20002,	// 
	FPNN_EC_CORE_TIMEOUT: 20003,			// 
	FPNN_EC_CORE_UNKNOWN_METHOD: 20004,		// 
	FPNN_EC_CORE_ENCODING: 20005,			// 
	FPNN_EC_CORE_DECODING: 20006,			// 
	FPNN_EC_CORE_SEND_ERROR: 20007,			// 
	FPNN_EC_CORE_RECV_ERROR: 20008,			// 
	FPNN_EC_CORE_INVALID_PACKAGE: 20009,	// 
	FPNN_EC_CORE_HTTP_ERROR: 20010,			// HTTP
	FPNN_EC_CORE_WORK_QUEUE_FULL: 20011,	// 
	FPNN_EC_CORE_INVALID_CONNECTION: 20012,	// 
	FPNN_EC_CORE_FORBIDDEN: 20013,			// 
	FPNN_EC_CORE_SERVER_STOPPING: 20014		// 
};

const FPNN_VERSION = Buffer.from([0x0, 0x1]);

const FP_FLAG = Buffer.from([
	0x40, //FP_FLAG_JSON
	0x80  //FP_FLAG_MSGPACK 
]);

const FP_MESSAGE_TYPE = Buffer.from([
	0x0, //FP_MT_ONEWAY
	0x1, //FP_MT_TWOWAY
	0x2  //FP_MT_ANSWER
]);

const TCP_MAGIC = Buffer.from('FPNN');
const HTTP_MAGIC = Buffer.from('POST');

const CRYPTO_CURVES = [
	'secp256k1',
	'secp224r1'
];

const CRYPTO_ALGORITHM = [
	'aes-128-cfb',
	'aes-256-cfb'
];

class FPConfig {

	static get BUFFER() {

		return Buffer;
	}

	static get ERROR_CODE() {

		return ERROR_CODE;
	}

	static get FP_FLAG() {

		return FP_FLAG;
	}

	static get FP_MESSAGE_TYPE() {

		return FP_MESSAGE_TYPE;
	}

	static get TCP_MAGIC() {

		return TCP_MAGIC;
	}

	static get HTTP_MAGIC() {

		return HTTP_MAGIC;
	}

	static get FPNN_VERSION() {

		return FPNN_VERSION;
	}

	static get READ_BUFFER_LEN() {

		return 1024;
	}

	static get CHECK_CBS_INTERVAL() {
		
		return 1000;
	}

	static get SEND_TIMEOUT() {

		return 20 * 1000;
	}
	
	static get CRYPTO_CURVES() {

		return CRYPTO_CURVES;
	}

	static get CRYPTO_ALGORITHM() {

		return CRYPTO_ALGORITHM;
	}
}

module.exports = FPConfig;